@media (max-width: 1320px) {
  section#hero {
    justify-content: space-evenly;
    gap: 0;
  }

  section#brands {
    gap: 0;
  }
  section#brands .contentBlock {
    padding: 4rem 0;
  }
}


@media (max-width: 1220px) {
  header#header {
  	justify-content: center;
  }
  #header #logo {
  	width: 14rem;
  }

  section#hero {
    flex-flow: column;
  }

  section#hero .searchBlock{
    padding: 0rem 2.4rem;
    gap: 1.4rem;
  }
  section#hero .searchBlock svg {
  	width: 24px;
  	height: 24px;
  }
  section#hero .searchBlock input {
  	padding: 1.6rem 0;
  }
  section#hero .searchBlock input::placeholder {
  	font-size: var(--fs-12);
  }

  section#hero section.left {
    gap: 2rem;
    text-align: center;
    transform: translateY(0px);
    margin-bottom: .5rem;
  }
  section#hero section.right section#playController {
    margin: 15px 0 0px;
  }

  section#brands {
    flex-flow: column;
    align-items: center;
    gap: 0;
  }
  section#brands .contentBlock {
    text-align: center;
    min-width: auto;
  }
  section#brands .gradientBG {
    display: none;
  }
  section#brands .brandsBlock img {
    max-height: 240px;
  }

  section#featuredProducts {
    padding-left: 0;
    padding-right: 0;
  }
  section#featuredProducts .wrapper {
    gap: 4rem;
    margin: 4.8rem 0;
  }


  section#about {
    padding: 8rem 2rem;
    justify-content: space-around;
    gap: 4rem;
  }
  section#about section.photo {
  	width: 30rem;
  	height: 30rem;
  }

  footer#footer {
    padding: 4rem 0 5rem;
  }
  footer#footer section#search {
  	/* min-width: unset; */
    order: 3;
  }
  footer#footer section#search .label {
    display: none;
  }

  .link {
    margin-top: 2.4rem;
  }

}


@media (max-width: 720px) {
  .label {
    margin-bottom: 1.2rem;
  }

  section#hero {
    padding-bottom: 8rem;
  }
  section#hero section.left {
    gap: 2rem;
  }

  section#hero .searchBlock{
    border-width: 1.5px;
  }
  section#hero .searchBlock svg {
    width: 20px;
    height: 20px;
  }
  section#hero .searchBlock input {
    font-size: var(--fs-14);
    padding: 1.2rem 0;
  }
  section#hero .searchBlock input::placeholder {
    font-size: var(--fs-10);
  }

  section#hero section.right section#playController {
    margin: 30px 0 0px;
  }

  section#heroSteps {
    flex-flow: column;
    gap: 30px;
  }

  section#brands {
    padding: 0;
    overflow: hidden;
  }
  section#brands .contentBlock {
    padding-bottom: 3rem;
  }
  section#brands .brandsBlock img {
    min-width: 650px;
    max-height: unset;
  }
  section#brands .link {
    display: none;
  }

  section#featuredProducts {
    padding: 12rem 0rem 8rem;
  }
  section#featuredProducts .titleBlock {
    margin-bottom: 3rem;
  }
  section#featuredProducts .wrapper {
    gap: 3rem;
    flex-flow: row nowrap;
    overflow: scroll;
    justify-content: flex-start;
    margin: 0rem;
    padding: 0 4.8rem 4rem;
  }
  section#featuredProducts .link {
    margin-top: 1rem !important;
  }
  section#featuredProducts div.productCard {
    width: 26rem;
  }
  div.productCard header {
    padding: 1.4rem;
  }
  div.productCard header .photoBlock {
    height: 20rem;
  }
  div.productCard header img {
    width: auto;
    height: 15rem;
  }
  div.productCard footer {
    height: initial;
    padding: 0.4rem 2rem 2rem;
    gap: 1.8rem;
  }
  div.productCard footer .detailsBlock {
    gap: 1.2rem;
  }
  div.productCard footer .detailsBlock h5 {
    font-size: var(--fs-12);
  }
  div.productCard footer .priceBlock span {
    font-size: var(--fs-14);
  }
  div.productCard footer .buttonsBlock {
    gap: 1.2rem;
    /* flex-flow: column; */
    width: 100%;
  }
  div.productCard footer .buttonsBlock a {
    justify-content: center;
    flex: 1;
    gap: 1.2rem;
  }
  div.productCard footer .buttonsBlock a.play span {
    width: 35px;
    white-space: nowrap;
  }


  section#about section.photo {
    width: 25rem;
    height: 25rem;
  }

  section#faqs {
    padding: 8rem 0 6rem;
  }
  section#faqs .contentBlock {
    padding: 2.4rem 2rem;
    margin-top: 3rem;
  }
  .faq .question {
    font-size: var(--fs-16);
    padding: 2rem 5rem 2rem 1rem;
  }
  .faq .question::after {
    top: 1.5rem;
  }
  .faq .content p {
    font-size: var(--fs-15);
  }

  footer#footer section#search {
    min-width: 80%;
    order: 3;
  }

  /* Global Link Styles */
  .link {
    font-size: var(--fs-12);
    /* margin-top: 1.8rem !important; */
  }
  .link::before {
    background-position: left top -6px;
    transform: translateX(-50px) scale(.8);
  }
  .link:hover::before {
  	transform: translateX(-42px) scale(.8);
  }

  .viewMoreButton {
    margin-top: 1.6rem;
  }

  header#header {
    justify-content: space-between;
  }
  header#header.slide #logo {
    transform: translateX(4rem);
  }
  header#header.slide nav {
    transform: translateX(0px);
  }
  header#header nav {
    position: absolute;
    left: 6%;
    top: 2.4rem;
    flex-flow: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 0;
    border-radius: 1.2rem;
    min-width: 22rem;
    padding: 7rem 0 0rem;
    z-index: 5;
    backdrop-filter: blur(30px);
    background: rgba(255,255,255,.3);
    border: 1px solid rgba(23, 14, 65, 0.05);
    /* box-shadow: 1px 0px 0px 0px rgba(255, 255, 255, 0.30) inset, 0px 15px 30px 0px rgba(23, 14, 65, 0.03); */
    transition: transform  ease.5s;
    transform: translateX(-320px);
    box-shadow:
    2.8px 2.8px 2.2px rgba(0, 0, 0, 0.02),
    6.7px 6.7px 5.3px rgba(0, 0, 0, 0.028),
    12.5px 12.5px 10px rgba(0, 0, 0, 0.035),
    22.3px 22.3px 17.9px rgba(0, 0, 0, 0.042),
    41.8px 41.8px 33.4px rgba(0, 0, 0, 0.05),
    100px 100px 80px rgba(0, 0, 0, 0.07);
    /* box-shadow:
    1.4px 1.4px 2.2px rgba(0, 0, 0, 0.02),
    3.3px 3.3px 5.3px rgba(0, 0, 0, 0.028),
    6.3px 6.3px 10px rgba(0, 0, 0, 0.035),
    11.2px 11.2px 17.9px rgba(0, 0, 0, 0.042),
    20.9px 20.9px 33.4px rgba(0, 0, 0, 0.05),
    50px 50px 80px rgba(0, 0, 0, 0.07); */
  }
  header#header nav a {
    width: 100%;
    padding: 2rem 4rem;
    border: 0px solid white;
    border-bottom: 1px solid rgba(16, 0, 43, .1);
    border-radius: 0;
    border-right-width: 0;
    border-left-width: 0;
    text-align: center;
    }
    header#header nav a:hover {
      background-color: rgba(252, 248, 255, .4);
    }
  header#header svg {
    margin: 2rem auto;
  }

  header #mobileMenu {
  		display: block
  }

}
